@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, main, body {
font-family: 'Lato', sans-serif;
background-color: #fcd12a;
z-index: 2;
}

.front,
.back {
  position: absolute;
  width: 100%;
}
.back {
  opacity: 0.1;
  min-width: 100%;
  min-height: 100%;
  background-image: url("https://tse1.mm.bing.net/th?id=OIP.SxMINsWHO_KOizmhGfFwNQHaHa&pid=Api&P=0&w=300&h=300");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

* {
  z-index: -1;
}
.containers {
  width: 100%;
  margin: 0;
  z-index: -1;
}
.image-stack {
  position: relative;
}
.image-bottom {
  position: absolute;
  z-index: -1;
  width: 100%;
  opacity: 0.9;
  display: block;
}


.img-2 {
    border-radius: 100px;
    box-shadow: 15px 0px 10px -10px rgba(22, 131, 151, 0.7);
    border: 2px solid rgba(22, 131, 151, 0.7);
    width:15%; 
    height:15%;
    margin-top: 8%;
  }
  .image-top {
    padding-top: 20px;
    padding-right: 20%;
    padding-left: 15px;
    border-radius: 3px;
  }
* {
    z-index: 2;
  }
  .containers {
    width: 100%;
    margin: 0;
    /* z-index: 2; */
  }
  .image-stack-1 {
    position: absolute;
    bottom: 0;
  }
  .image-stack__item--bottom {
    position: absolute;
    width: 100%;
    z-index: -1;
    opacity: 0.9;
    display: block;
  }
  
  
